@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@400;600;700&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}

.page{background:#000;color:#fff;width:100%;height:100vh;caret-color:transparent;position:absolute}.title{font-weight:bold;font-size:52px}.toaster__container{position:absolute;z-index:2;width:350px;left:calc(50% - 185px);transform:translateY(-100%);transition:transform 1s}.toaster__content{width:100%;background:#d90429;padding:10px;height:100%;border-bottom-left-radius:20px;border-bottom-right-radius:20px;font-size:22px;display:flex;justify-content:center;flex-direction:column;align-items:center}.toaster__up{transform:translateY(0)}
.avatar{position:relative}.avatar__wrapper{width:220px;height:220px;border:8px solid #fff;border-radius:160px;background:#edf2f4;position:relative;overflow:hidden}.avatar__wrapper_small{width:32px;height:32px;border:4px solid #fff;border-radius:50%;background:#edf2f4;position:relative;overflow:hidden}.avatar--outined{border:8px solid #d90429}.avatar__refresh{width:60px;height:60px;background:#fff;position:absolute;top:180px;left:180px;border-radius:50%;border:none;cursor:pointer;display:flex;justify-content:center;align-items:center}.avatar__face{width:100%}
.button{height:56px;border-radius:20px;padding:0 72px;font-size:24px;background:#d90429;border:none;font-weight:bold;cursor:pointer;color:#fff}.button_disabled{pointer-events:none;background:#8d99ae}.button:hover{background:#ee0830;box-shadow:3px 4px 10px rgba(217,4,41,.36)}
.text{font-family:"Montserrat",sans-serif}
.home{display:flex;flex-direction:column;align-items:center}.home__container{max-width:717px;width:100%;display:flex;flex-direction:column;align-items:center;height:100%;justify-content:space-around}.home__avatar_container{width:100%;display:flex;flex-wrap:wrap;justify-content:space-around;align-items:center;margin-bottom:150px}.home__pseudo{height:48px;border-radius:5px;font-size:18px;font-weight:500;text-align:center;caret-color:#000}.home__pseudo_container{height:189px;display:flex;flex-direction:column;justify-content:space-between}@media(max-width: 541px){.avatar_container{align-items:center}.pseudo_container{margin-bottom:0px}}
.lobby{display:flex;justify-content:center}.lobby__container{max-width:590px;width:100%;padding:0 40px;display:flex;flex-direction:column;justify-content:space-around;align-items:center}.lobby__invite{height:83px;display:flex;flex-direction:column;justify-content:space-between;align-items:center}.lobby__list{height:280px;max-width:400px;width:100%;border-top:#fff 1px solid}.lobby__item{height:54px;border-bottom:#fff 1px solid;display:flex;align-items:center;font-size:20px;padding:0 12px}.lobby__spacing{width:22px}.lobby__svg{height:32px;fill:#fff;margin-left:auto}.lobby__svg_clickable{cursor:pointer}.lobby__start_message{font-weight:bold;font-size:28px}.lobby__start_container{width:100%;display:flex;justify-content:space-between;align-items:center;flex-wrap:wrap;grid-gap:12px;gap:12px}@media(max-width: 636px){.lobby_item_mobile{justify-content:space-around}}
.waiting{font-size:22px}
.question{max-width:590px;height:100%;width:100%;display:flex;flex-direction:column;align-items:center}.question__header{height:128px;border-bottom:solid #fff 1px;width:100%}.question__groupe{height:240px;width:100%;display:flex;flex-direction:column;justify-content:space-between;align-items:center}.question__main{display:flex;justify-content:space-around;flex-direction:column;height:100%}.question__info{font-weight:500;font-size:18px}.question__info{font-weight:500;font-size:18px;text-align:center}.question__title{font-weight:600;font-size:28px;text-align:center;margin-top:16px}
.buttonQ{width:366px;height:48px;border-radius:48px;padding:0px 10px;color:#000;font-weight:bold;font-size:20px;display:flex;justify-content:center;align-items:center;border:none;cursor:pointer}.buttonQ__normal{background:#fff}.buttonQ__valide{background:#44e786}.buttonQ__false{background:#fc6363}
.board{display:flex;justify-content:center}.timer{position:absolute;color:#fff;font-size:120px;left:45%;top:40%}
.player{display:flex;flex-direction:column;align-items:center;font-size:20px;margin:10px}.player__bar{margin-top:20px;display:flex;align-items:center}.player__spacer{border-right:solid 1px #fff;height:30px;margin:0 10px}.player__card{width:16px;height:25px;border-radius:3px;margin-right:10px;background:#fff}
